import Utils from './Utils';
class OkuriganaUtils {
    static format(surface, kana) {
        if(surface === kana || kana.length === 0) {
            return surface;
        }
        if(Utils.isPureAlpha(surface)) {
            return surface;
        }
        return this.align(surface, kana)
    }

    static divideSurface(surface) {
        let ret = [];
        let t = [surface[0]];
        
        for(let i = 1; i < surface.length; i++) {
            if(!Utils.isHiragana(surface[i]) === !Utils.isHiragana(surface[i - 1])) {
                t.push(surface[i]);
            } else {
                ret.push(t.join(''));
                t = [surface[i]];
            }
        }
        ret.push(t.join(''));

        return ret;
    }
    
    static align(surface, kana) {
        let s = this.divideSurface(surface);
        let ary = Array(s.length);
        let len = ary.length;
        if(len === 1) {
            return `${surface}(${kana})`;
        } else if(len === 2) {
            if(Utils.isKanji(s[0][0])) {
                let idx = kana.lastIndexOf(s[1]);
                return `${s[0]}(${kana.substr(0,idx)})${s[1]}`;
            } else {
                let idx = s[0].length;
                return `${s[0]}${s[1]}(${kana.substr(idx)})`;
            }
        } else if(len === 3) {
            if(Utils.isKanji(s[0][0])) {
                let idx = kana.slice(1, -1).indexOf(s[1]) + 1;
                return `${s[0]}(${kana.substr(0,idx)})${s[1]}${s[2]}(${kana.substr(idx + s[1].length)})`;
            } else {
                let _kana = kana.slice(s[0].length).slice(0, -s[2].length);
                return `${s[0]}${s[1]}(${_kana}${s[2]})`;
            }
        } else if(len === 4 && Utils.isKanji(s[0][0])) {
            let _kana = kana.slice(0, -s[3].length);
            let ret = this.align(s.slice(0, 3).join(''), _kana);
            ret += s[3];
            return ret;
        }
    }
}

export default OkuriganaUtils